<template>
    <div>
        <md-toolbar id="nav-bar" class="md-primary" md-elevation="1">
            <md-button><router-link id="Home" to="/">Home</router-link></md-button>
            <md-button v-on:click="refresh">Refresh</md-button>
        </md-toolbar>
        <br>
        <br>
        <br>
        <div>
            <p id="name">{{ayahs.surah.englishName}}</p>
        </div>
        <md-table id="table">
            <md-table-row>
                <md-table-cell id="number">{{ayahs.numberInSurah}}</md-table-cell>
                <md-table-cell id="text" md-numeric>{{ayahs.text}}</md-table-cell>
            </md-table-row>
        </md-table>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    name: "Kursi",
    data(){
        return{
            ayahs:[],
            loading: false
        }
    },
    methods:{
        refresh:function(){
            location.reload()
        },
        mediaQuery:function(){
            if(screen.width < 480){
                document.getElementById('text').style.fontSize = 30+"px"
                document.getElementById('number').style.fontSize = 30+"px"
            }
        }
    },
    mounted(){
        this.mediaQuery()
        this.loader()
    },
    created(){
        axios.get('https://api.alquran.cloud/v1/ayah/262').then(response =>{
            this.ayahs = response.data.data
        })
    }
}
</script>
<style scoped>
#text{
    color: #42b983;
    font-size: 30px;
    line-height: 50px;
    margin: 0;
}
#number{
    color: #42b983;
    font-size: 30px;
}
#nav-bar{
  background: #42b983;
  position: fixed;
}
#Home{
    color: #fff;
    text-decoration: none;
}
#name{
    color: #42b983;
    font-size: 30px;
}
</style>


