<template>
    <div class="hello">
        <md-toolbar id="nav-bar" class="md-primary" md-elevation="1">
            <md-button v-on:click="home"><router-link id="Home" to="/">Home</router-link></md-button>
            <md-button class="md-primary" v-on:click="refresh" @click="refresh">Refresh</md-button>
            <md-button class="md-primary" @click="stopScroll" v-if="scrollStopActive">Stop</md-button>
            <md-button class="md-primary" @click="startScroll" v-if="scrollStartActive">Start</md-button>
        </md-toolbar>
        <br>
        <br>
        <br>
        <br>
        <md-dialog-confirm
            :md-active.sync="alertActive"
            md-title="Type Of Scrolls"
            md-content="Auto-Scroll Does Scroll Automatically||Manual-Scroll Does Nothing"
            md-confirm-text="Auto-Scroll"
            md-cancel-text="Manual-Scroll"
            @md-confirm="startScroll" />
        <!-- content -->
        <Loader v-if="loading"/>
        <md-table>
            <md-table-row id="content" :key="index" v-for="(i,index) in getSurah">
                <md-table-cell id="number">{{i.numberInSurah}}</md-table-cell>
                <md-table-cell id="text" md-numeric>{{i.text}}</md-table-cell>
            </md-table-row>
        </md-table>
    </div>
</template>
<script>
import axios from 'axios';
import Loader from './Loader.vue';
export default {
    name: 'QuranDetail',
    components:{
        Loader
    },
    data(){
        return{
            intervalId: '',
            getSurah: [],
            loading: false,
            loading: false,
            alertActive:false,
            scrollStartActive:false,
            scrollStopActive:false,
        }
    },
    methods:{
        refresh:function(){
            location.reload();
        },
        loader:function(){
            this.loading  = !false
            setTimeout(()=>{
                this.loading = !true
            },1000)
        },
        startScroll(){
            this.scrollStopActive = !false
            this.scrollStartActive = !true
            this.intervalId = setInterval(() => {
                window.scrollBy(0, 2)
            }, 30);
            return this.intervalId;
        },
        stopScroll(){
            this.scrollStartActive = !false
            this.scrollStopActive = !true
            clearInterval(this.intervalId)
        },
        home:function(){
            this.stopScroll()
            this.scrollStartActive = !true
        },
    },  
    mounted(){
        this.loader()
        this.alertActive = !false
        window.onpopstate = function(){
            location.reload()
        }
    },
    created(){
        let id = this.$route.params.id
        axios.get('https://api.alquran.cloud/v1/surah/'+id).then(response => {
            this.getSurah = response.data.data.ayahs
        })
    }

}

</script>
<style scoped>
#nav-bar{
  background: #42b983;
  position: fixed;
}
#Home{
  color: #fff;
  text-decoration: none;
}
#list{
    color: #42b983;
    font-size: 50px;
    font-weight: bolder;
}
#row{
    margin-bottom: 80px;
}
#text{
    color: #42b983;
    font-size: 30px;
    line-height: 50px;
    margin: 0;
}
#number{
    color: #42b983;
    font-size: 25px;
}
</style>


