<template>
  <div class="hello">
    <md-toolbar id="nav-bar" class="md-primary" md-elevation="1">
      <md-button><router-link id="Home" to="/">Home</router-link></md-button>
      <md-button v-on:click="refresh">Refresh</md-button>
    </md-toolbar>
    <br />
    <br />
    <br />
    <div class="input-wrapper">
      <input type="text" v-model="search" placeholder="Search eg:Yaseen" />
      <i class="fas fa-search"></i>
    </div>
    <p><b>POPULAR SEARCHES</b></p>
    <div class="popular-searches" id="popular">
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/36">Yaseen</router-link></md-chip
      >
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/67">Al-Mulk</router-link></md-chip
      >
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/55">Ar-Rahmaan</router-link></md-chip
      >
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/56">Al-Waaqia</router-link></md-chip
      >
      <br />
      <br />
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/18">Al-Kahf</router-link></md-chip
      >
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/surah/73"
          >Al-Muzzammil</router-link
        ></md-chip
      >
      <md-chip class="md-primary" id="chips" md-clickable
        ><router-link id="Home" to="/ayah/ayatul-kursi"
          >Ayatul-Kursi</router-link
        ></md-chip
      >
    </div>
    <md-card md-with-hover :key="i.id" v-for="i in filteredQuran">
      <md-ripple>
        <md-card-header>
          <div class="md-title">{{ i.englishName }}</div>
          <div class="md-subhead" id="Subhead">
            <b>{{ i.name }}</b>
          </div>
        </md-card-header>

        <md-card-content>
          {{ i.englishNameTranslation }}
        </md-card-content>

        <md-card-content>
          <span>RevelationType: </span>{{ i.revelationType }}
        </md-card-content>

        <md-card-content>
          <span>No Of The Surah: </span>{{ i.number }}
        </md-card-content>

        <md-card-actions>
          <md-button
            ><router-link id="View" :to="'/surah/' + i.number"
              >View</router-link
            ></md-button
          >
        </md-card-actions>
      </md-ripple>
    </md-card>
    <Loader id="Loading" v-if="loading" />
  </div>
</template>

<script>
import axios from "axios";
import Loader from "./Loader.vue";
export default {
  name: "QuranList",
  components: {
    Loader
  },
  data() {
    return {
      surahs: [],
      loading: false,
      search: ""
    };
  },
  methods: {
    refresh: function() {
      location.reload();
    },
    loader: function() {
      this.loading = !false;
      setTimeout(() => {
        this.loading = !true;
      }, 2000);
    }
  },
  mounted() {
    this.loader();
  },
  computed: {
    filteredQuran: function() {
      return this.surahs.filter(surah => {
        return surah.englishName.match(this.search);
      });
    }
  },
  created() {
    axios
      .get("https://api.alquran.cloud/v1/surah")
      .then(response => {
        this.surahs = response.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.md-card {
  width: 320px;
  margin: 10px;
  margin-top: 80px;
  display: inline-block;
  vertical-align: top;
}
#Home {
  color: #fff;
  text-decoration: none;
}
#nav-bar {
  background: #42b983;
  position: fixed;
}
.md-toolbar + .md-toolbar {
  margin-top: 16px;
}
.md-title {
  color: #42b983;
}
#View {
  color: #42b983;
  text-decoration: none;
}
#Subhead {
  font-size: 18px;
}
#Loading {
  margin-top: 100px;
}
.input-wrapper {
  margin-top: 50px;
  padding: 0.5rem;
  border-radius: 0.5rem;
}
input {
  margin: 0 0.5rem 0 0.5rem;
  border: none;
  outline: none;
  padding: 15px 20px;
  color: #42b983;
}
::placeholder {
  color: #42b983;
  text-align: center;
}
#chips {
  background-color: #42b983;
}
.fas {
  background-color: #42b983;
  padding: 15px;
  color: #fff;
  margin: 0;
}
</style>



