import Vue from "vue";
import Router from "vue-router";
import QuranList from "@/components/QuranList";
import QuranDetail from "@/components/QuranDetail";
import Kursi from "@/components/Kursi.vue";
import NotFound from "@/components/404NOTFOUND.vue";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "QuranList",
      component: QuranList
    },
    {
      path: "/surah/:id/",
      name: "QuranDetail",
      component: QuranDetail
    },
    {
      path: "/ayah/ayatul-kursi",
      name: "Kursi",
      component: Kursi
    },
    {
      path: "*",
      name: "NotFound",
      component: NotFound
    }
  ]
});



